/*! modern-normalize v2.0.0 | MIT License | https://github.com/sindresorhus/modern-normalize */
*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  font-family: system-ui, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  line-height: 1.15;
}

body {
  margin: 0;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted buttontext;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@font-face {
  font-family: montserrat;
  src: url("Montserrat-Regular.5c4ab150.ttf");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: montserrat;
  src: url("Montserrat-SemiBold.fab6947e.ttf");
  font-style: normal;
  font-weight: 600;
}

@font-face {
  font-family: montserrat;
  src: url("Montserrat-Bold.1b1f71c4.ttf");
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: montserrat;
  src: url("Montserrat-ExtraBold.1022d027.ttf");
  font-style: normal;
  font-weight: 800;
}

:root {
  --main-text-color: #60697b;
  --light-text-color: #f9fafb;
  --main-bg-color: #fff;
  --main-accent-color: #3a6ee1;
  --secondary-accent-color: #04cb64;
  --add-first-color: #ecf404;
  --add-second-color: #3a6fe12e;
  --add-third-color: #3293b2;
  --btn-bg-color: #f9fafb;
  --backdrop: #0a13276d;
}

body {
  color: var(--main-text-color);
  background: var(--main-bg-color);
  font-family: montserrat, sans-serif;
  font-weight: 400;
}

.page-container {
  min-height: 100vh;
  position: relative;
}

.content-wrap {
  padding-bottom: 250px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

.visually-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: 0;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.backdrop {
  background-color: var(--backdrop);
  width: 100vw;
  height: 100vh;
  transition: opacity .25s cubic-bezier(.23, .86, .79, .77), visibility .25s cubic-bezier(.23, .86, .79, .77);
  position: fixed;
  top: 0;
}

.line {
  background-color: var(--main-accent-color);
  width: 140px;
  height: 2px;
  margin: 0 auto;
}

.base-btn {
  letter-spacing: .02em;
  color: var(--light-text-color);
  border: 1px var(--light-text-color) solid;
  background-color: #0000;
  border-radius: 32px;
  padding: 14px 28px;
  font-family: inherit;
  font-size: 14px;
  line-height: 1.4;
  transition: color .25s cubic-bezier(.23, .86, .79, .77), background-color .25s cubic-bezier(.23, .86, .79, .77);
  display: inline-block;
}

.base-btn:hover, .base-btn:focus {
  color: var(--main-text-color);
  background-color: var(--btn-bg-color);
  outline: none;
}

@media screen and (width >= 375px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }
}

.header {
  background-color: var(--main-accent-color);
  width: 100%;
  padding: 16px 0;
  position: fixed;
  top: 0;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  display: flex;
}

.header-logo {
  color: var(--light-text-color);
  font-size: 36px;
  font-weight: 600;
}

.header-nav {
  display: none;
}

.header-menu-btn {
  background-color: #0000;
  border: none;
  outline: none;
  padding: 4px;
  display: flex;
}

.mobile-menu {
  background-color: var(--main-bg-color);
  width: 70vw;
  height: 100vh;
  padding: 16px 15px;
  transition: transform .25s cubic-bezier(.23, .86, .79, .77);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(0);
}

.backdrop.is-hidden .mobile-menu {
  transform: translateX(100%);
}

.mobile-close-btn {
  background-color: #0000;
  border: none;
  outline: none;
  margin: 0 0 30px auto;
  padding: 4px;
  display: flex;
}

.mobile-list {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.mobile-item {
  letter-spacing: .02em;
  text-align: center;
  color: var(--main-text-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.4;
  transition: color .25s cubic-bezier(.23, .86, .79, .77);
}

@media screen and (width >= 375px) {
  .header-container {
    padding: 0 20px;
  }

  .mobile-menu {
    padding: 16px 20px;
  }
}

@media screen and (width >= 768px) {
  .header-container {
    padding: 0 32px;
  }

  .header-nav {
    display: flex;
  }

  .header-list {
    gap: 40px;
    display: flex;
  }

  .header-item {
    letter-spacing: .02em;
    text-align: center;
    color: var(--light-text-color);
    cursor: pointer;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.4;
    transition: color .25s cubic-bezier(.23, .86, .79, .77);
  }

  .header-item:hover {
    color: var(--secondary-accent-color);
  }

  .header-menu-btn {
    display: none;
  }

  @media screen and (width >= 1280px) {
    .header-container {
      padding: 0 100px;
    }
  }
}

.hero-section {
  background-color: var(--main-accent-color);
  color: var(--light-text-color);
  padding: 100px 0 80px;
}

.hero-title {
  text-align: center;
  text-transform: uppercase;
  background: linear-gradient(to right, var(--add-first-color), var(--secondary-accent-color), var(--light-text-color));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  margin: 0 auto 40px;
  font-size: 42px;
  font-weight: 800;
  line-height: 1.4;
}

.hero-descrip {
  letter-spacing: .02em;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 60px;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.4;
}

.hero-btn {
  text-align: center;
}

@media screen and (width >= 375px) {
  .hero-title {
    letter-spacing: .02em;
    font-size: 48px;
  }
}

.services-section {
  padding-top: 60px;
}

.services-title {
  text-align: center;
  letter-spacing: .02em;
  text-transform: uppercase;
  background: linear-gradient(to right, var(--main-accent-color), var(--secondary-accent-color));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  margin-bottom: 28px;
  font-size: 36px;
  line-height: 1.4;
}

.services-list {
  flex-direction: column;
  gap: 20px;
  margin-bottom: 60px;
  display: flex;
}

.services-item {
  background: radial-gradient(farthest-side at 30% 30%, var(--main-bg-color), var(--add-second-color) 120%);
  border-radius: 10px;
  padding: 28px 20px;
}

.services-item-title {
  text-align: center;
  letter-spacing: .02em;
  text-transform: uppercase;
  width: 200px;
  margin: 0 auto 18px;
  font-size: 18px;
  line-height: 1.4;
}

.services-item-desc {
  text-align: center;
  letter-spacing: .02em;
  font-size: 16px;
  line-height: 1.4;
}

@media screen and (width >= 768px) {
  .services-list {
    flex-flow: wrap;
    justify-content: space-between;
    gap: 16px;
  }

  .services-item {
    width: calc(50% - 10px);
  }
}

.process-section {
  padding-top: 60px;
}

.process-title {
  letter-spacing: .02em;
  text-transform: uppercase;
  text-align: center;
  background: linear-gradient(to right, var(--main-accent-color), var(--secondary-accent-color));
