  font-size: 18px;
  line-height: 1.4;
}

.process-list {
  flex-direction: column;
  margin-bottom: 60px;
  display: flex;
}

.process-item {
  padding: 22px 12px;
}

.process-svg {
  margin: 0 auto 12px;
  display: block;
}

.process-item-title {
  text-align: center;
  letter-spacing: .02em;
  width: 200px;
  margin: 0 auto 8px;
  font-size: 18px;
  line-height: 1.4;
}

.process-item-desc {
  text-align: center;
  letter-spacing: .02em;
  max-width: 550px;
  margin: 0 auto;
  font-size: 16px;
  line-height: 1.4;
}

.process-container {
  background-color: var(--secondary-accent-color);
  border-radius: 18px;
  width: 100%;
  margin-top: 60px;
  padding: 50px 0;
}

.process-container-text {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.process-text {
  letter-spacing: .02em;
  text-align: center;
  color: var(--light-text-color);
