  line-height: 1.4;
}

@media screen and (width >= 768px) {
  .process-text {
    font-size: 40px;
  }
}

@media screen and (width >= 1280px) {
  .process-list {
    flex-flow: wrap;
    justify-content: center;
  }

  .process-item {
    width: 300px;
  }

  .process-text {
    font-size: 46px;
