}

.features-item-title {
  letter-spacing: .02em;
  margin: 0 auto 8px;
  font-size: 18px;
  line-height: 1.4;
}

.features-item-desc {
  letter-spacing: .02em;
  font-size: 16px;
  line-height: 1.4;
}

@media screen and (width >= 768px) {
  .features-item {
    margin: 0 auto;
  }
}

@media screen and (width >= 1280px) {
  .features-list {
    flex-direction: row;
    display: flex;
  }

  .features-item {
    width: calc(33.3333% - 13.3333px);
  }
}

.footer {
  background-color: var(--add-third-color);
  width: 100%;
  padding: 30px 0;
  position: absolute;
  bottom: 0;
}

.footer-container {
  padding: 0 15px;
}

.footer-logo-container {
  justify-content: space-between;
  display: flex;
}

.footer-logo {
  color: var(--light-text-color);
  margin-bottom: 24px;
  font-size: 20px;
