}

.footer-btn {
  flex-shrink: 0;
}

.footer-registration {
  letter-spacing: .02em;
  color: var(--light-text-color);
  margin-bottom: 12px;
  font-size: 12px;
  line-height: 1.4;
}

.footer-copy {
  letter-spacing: .02em;
  color: var(--light-text-color);
  font-size: 12px;
