.contacts-container {
  flex-direction: column;
  gap: 40px;
  padding: 120px 0;
  display: flex;
}

.contacts-title {
  letter-spacing: .02em;
  text-transform: uppercase;
  background: linear-gradient(to right, var(--main-accent-color), var(--secondary-accent-color));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  margin-bottom: 18px;
  font-size: 30px;
  line-height: 1.4;
}

.contacts-adrress {
  letter-spacing: .02em;
  margin-bottom: 18px;
  font-size: 16px;
  font-style: normal;
  line-height: 1.4;
}

.contacts-tel {
  letter-spacing: .02em;
  font-size: 16px;
  line-height: 1.4;
}

.contacts-form-container {
  background: radial-gradient(farthest-side at 30% 30%, var(--main-bg-color), var(--add-second-color) 120%);
  border-radius: 10px;
  padding: 28px 20px;
}

.contacts-form {
  flex-direction: column;
  display: flex;
}

.contacts-label {
  letter-spacing: .02em;
  margin-bottom: 2px;
  font-size: 14px;
  line-height: 1.4;
}

.contacts-input {
  letter-spacing: .02em;
  color: var(--main-text-color);
  border: 1px solid var(--add-second-color);
  background-color: var(--btn-bg-color);
  border-radius: 8px;
  height: 46px;
  margin-bottom: 12px;
  padding: 10px 16px;
  font-size: 16px;
  line-height: 1.4;
}

.contacts-input:focus {
  box-shadow: var(--add-second-color) 0px 2px 8px 0px;
  outline: none;
}

.contacts-textarea {
  letter-spacing: .02em;
  border: 1px solid var(--add-second-color);
  background-color: var(--btn-bg-color);
  color: var(--main-text-color);
  resize: none;
  border-radius: 8px;
  height: 120px;
  margin-bottom: 12px;
  padding: 10px 16px;
  font-size: 16px;
  line-height: 1.4;
}

.contacts-textarea:focus {
  box-shadow: var(--add-second-color) 0px 2px 8px 0px;
  outline: none;
}

.contacts-btn {
  letter-spacing: .02em;
  color: var(--main-text-color);
  border: 1px var(--add-second-color) solid;
  background-color: #0000;
  border-radius: 32px;
  width: 140px;
  padding: 14px 28px;
  font-family: inherit;
  font-size: 14px;
  line-height: 1.4;
  transition: border .25s cubic-bezier(.23, .86, .79, .77), background-color .25s cubic-bezier(.23, .86, .79, .77), box-shadow .25s cubic-bezier(.23, .86, .79, .77);
}

.contacts-btn:hover, .contacts-btn:focus {
  background-color: var(--btn-bg-color);
  border: 1px var(--btn-bg-color) solid;
  box-shadow: var(--add-second-color) 0px 2px 8px 0px;
  outline: none;
}

@media screen and (width >= 1280px) {
  .contacts-container {
    flex-direction: row;
    justify-content: space-between;
  }

  .contacts-form-container {
    width: 620px;
  }
}

